// SUPER_ADMINISTRATOR = 27;    //超级管理员;
// ORG_ADMINISTRATOR = 28;      //组织管理员;
// SCHOOL_ADMINISTRATOR = 29;    //学校管理员;
// PARENT = 30;          //家长;
// PRINCIPAL = 31;          //园长;
// TEACHER = 32;          //老师;
// DOCTOR = 33;          //医生;
// SERVICE = 34;          //后勤;
// ASSISTANT = 35;          //行政;
import header from '@/components/header';
export default {
  data() {
    return {
      roleList: [],
      titleContent: '选择角色'
    }
  },
  created() {
    // 设置角色列表进入对应角色主页
    this.classMap = ['/super','/org','/school','/parent','/principal','/teacher','/doctor','/service','/assistant'];
    //获取角色列表
    this.$http.post(`${WX_URL}/base/login/getRoleSelect/`,{'mobile': '13699009629','openid': openid}).then((response) => {
      response = response.data
      if (response.success && response.code < 2000) {
        //赋值数据
        this.roleList = response.data;
        //若用户只有一种身份，则根据用户的类型进入相应的角色主页
        if (this.roleList.length === 1) {
          localStorage.setItem('USERLIST', JSON.stringify(this.roleList[0]));
          this.goToIndex(this.roleList[0].roleType);
        }
      }else if (response.code > 2000) {
        //用户数据获取失败，跳到绑定页面
        this.$router.push('/bindChooseRole');
      }
    });

  },
  methods: {
    //直接跳转到相应的角色主页
    goToIndex(roleType){
      this.$router.push(this.classMap[roleType-1]);
    },
    selectRole(role) {
      localStorage.setItem('USERLIST', JSON.stringify(role));
    }
  },
  components: {
    'v-header':header
  }
}




