import teacherApi from '@/api/attendance/teacher'
import {loadingState, successState, errorState} from './../../utils'

const state = {
  // 老师主页信息
  teacherAttendance: {
    allNum: 0,
    arriveNum: 0,
    classId: 0,
    noArriveNum: 0,
  },
  // 学生列表信息
  studentAttendance: {},
  // 老师考勤类型
  teacherAttendanceType: [
    {state: '迟到', color: "#ff0000"},
    {state: '早退', color: "#00ffff"},
    {state: '缺勤', color: '#23fe23'}
  ],
  // 老师考勤状态
  teacherAttendanceColor: {
    1: "#ff0000",
    2: "#00ffff",
    3: '#23fe23'
  }
}

// getter
const getters = {
  getTeacherAttendance: state => state.teacherAttendance,
  getStudentAttendance: state => state.studentAttendance,
  getTeacherAttendanceType: state => state.teacherAttendanceType,
  getTeacherAttendanceColor: state => state.teacherAttendanceColor
}

// action
const actions = {
  actionTeacherAttendance (context, formData) {
    context.commit('changeLingLoading', loadingState)
    teacherApi.ClassAttendanceFindtByTeacher(formData, response => {
      context.commit('changeLingLoading', successState)
      context.commit('mutationTeacherAttendance', response.data.data)
    }, response => {
      setTimeout(function(){
        context.commit('changeLingLoading', errorState(response))
      }, 200)
    })
  },
  actionStudentAttendance (context, formData) {
    context.commit('changeLingLoading', loadingState)
    teacherApi.StudentAttendanceFindtByClass(formData, response => {
      context.commit('changeLingLoading', successState)
      context.commit('mutationStudentAttendance', response.data.data)
    }, response => {
      setTimeout(function(){
        context.commit('changeLingLoading', errorState(response))
      }, 200)
    })
  }
}

// mutations
const mutations = {
  // 更新老师的出勤信息
  mutationTeacherAttendance (state, teacherAttendance) {
    state.teacherAttendance = teacherAttendance
  },
  // 更新学生的出勤信息
  mutationStudentAttendance (state, studentAttendance) {
    state.studentAttendance = studentAttendance
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



