import Vue from 'vue'
import Vuex from 'vuex'
import state from './state'
import getters from './getters'
import mutations from './mutations'
import teacher from './modules/attendance/teacher'
import principal from './modules/attendance/principal'
import pickertime from './modules/pickertime/pickertime'

Vue.use(Vuex)

export default new Vuex.Store({
  state,
  getters,
  mutations,
  modules: {
    teacher,
    principal,
    pickertime
  }
})



