export default {
  /**
   * 请求过程的状态变化，
   * isShow： 【true, false】 是否显示加载或错误信息, 相当于数据是否获取成功
   * message: [String] 显示文字
   * state: 【'loading', 'error', 'success'】 所处状态
   */
  lingLoading: {
    isShow: true,
    message: '加载中',
    state: 'loading'
  }
}




